
import { User, Difficulty, WorkoutFocus } from '../types';

export interface FrequencyRecommendation {
  days: number;
  reason: string;
  isRestricted: boolean;
}

export const calculateRecommendedFrequency = (user: User): FrequencyRecommendation => {
  const hasInjuries = user.injuries.length > 0;
  const isSenior = user.age > 60;
  const highStress = user.stress_level > 3;
  const poorSleep = user.sleep_quality < 3;

  // 1. RESTRICCI√ìN M√âDICA Y RECUPERACI√ìN CR√çTICA
  if (hasInjuries || isSenior || (highStress && poorSleep)) {
    let reason = "";
    if (hasInjuries || isSenior) {
      reason = isSenior 
        ? "Priorizamos la longevidad articular con 3 d√≠as de Full Body."
        : "Recomendamos 3 d√≠as para asegurar que tus lesiones tengan tiempo de sanar entre est√≠mulos.";
    } else {
      reason = "Tus niveles de estr√©s y falta de sue√±o sugieren que tu sistema nervioso necesita m√°s descanso. 3 d√≠as es lo √≥ptimo para evitar el sobreentrenamiento.";
    }

    return {
      days: 3,
      reason,
      isRestricted: true
    };
  }

  // 2. NIVEL DE EXPERIENCIA
  if (user.fitness_level === Difficulty.BEGINNER) {
    return {
      days: 3,
      reason: "Como principiante, tu cuerpo necesita adaptarse gradualmente. 3 d√≠as permiten una progresi√≥n segura.",
      isRestricted: false
    };
  }

  // 3. OBJETIVOS Y ESTILO DE VIDA (MODERADO)
  if (user.fitness_level === Difficulty.ADVANCED && !highStress) {
    if (user.goal === WorkoutFocus.HYPERTROPHY || user.goal === WorkoutFocus.PERFORMANCE) {
      return {
        days: 5,
        reason: "Tu base f√≠sica y bajo estr√©s permiten un volumen de 5 d√≠as para resultados m√°ximos.",
        isRestricted: false
      };
    }
  }

  return {
    days: 4,
    reason: "4 d√≠as es el 'punto dulce' para equilibrio entre trabajo, vida personal y progreso f√≠sico.",
    isRestricted: false
  };
};
